.btnAddItem {
    margin-bottom: 2rem;

    button {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
        width: 70%;
        margin: auto;
        padding: 12px;
        font-size: 14px;
        font-weight: 400;
        border-radius: 8px;
        color: rgb(255, 255, 255);
        background: #131822;
        margin: auto;

        &:hover {
            background: #fe8f00;
        }

        img {
            width: 16px;
            height: 16px;
        }
    }
}
